import { useAuth } from 'hooks';
import {
  BurgerMenuContainer,
  StyledBtnLogOut,
  StyledNavLink,
} from './BurgerMenu.styled';
import { logOut } from 'redux/auth/operations';
import { useDispatch } from 'react-redux';

const BurgerMenu = () => {
  const { isLoggedIn, userType } = useAuth();
  const dispatch = useDispatch();

  const hendlelogout = () => {
    dispatch(logOut());
  };
  return (
    <BurgerMenuContainer>
      {isLoggedIn ? (
        <>
          <StyledNavLink to={`/user/${userType}`}> </StyledNavLink>
          <StyledBtnLogOut onClick={() => hendlelogout()}>
            
          </StyledBtnLogOut>
        </>
      ) : (
        <>
          <>
            <StyledNavLink to="/login"></StyledNavLink>
            <StyledNavLink to="/register"></StyledNavLink>
          </>
        </>
      )}
    </BurgerMenuContainer>
  );
};

export default BurgerMenu;
